.igo-swipe-control-container {
  position: absolute;
  width: 80px;
  height: 100%;
  top: 0%;
  left: 50%;
  cursor: grab;
  z-index: 1;
  transform: translate(-40px, 0%);
}

.igo-swipe-control-container:active {
  cursor: grabbing;
}

.igo-swipe-control-line {
  position: absolute;
  width: 4px;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgb(94, 90, 90);
}

.igo-swipe-control-arrows {
  position: absolute;
  width: 32px;
  height: 32px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  margin-top: -16px;
  padding: 5px 4px;
  background-color: rgb(94, 90, 90);
  z-index: 2;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  div {
    display: inline-block;
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
  }

  .igo-arrow-right {
    margin-left: 4px;
    border-left: 10px solid white;
  }

  .igo-arrow-left {
    border-right: 10px solid white;
  }
}

.igo-swipe-control-arrows:active {
  display: none;
}
